<template lang="pug">
    div(class="page")
        Header
        section(class="careers")
            div(class="container")
                div(class="title")
                    h2: span job details
                div(class="careers-box wow fadeInUp" data-wow-duration="1s")
                    div(class="details")
                        h3 {{ career.title }}
                        div(v-html="career.body")
                        .clickable
                            router-link(to="/apply" class="btn hvr-sweep-to-bottom") Apply Now
        Footer
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";

import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

Vue.use(VueAxios, axios);

export default {
    name: "SingleCareer",
    components: {
        Header,
        Footer,
    },
    data() {
        return {
            url: this.$route.params.url,
            career: [],
        };
    },
    mounted() {
        this.axios.get("careers/" + this.url).then((singlCareerApi) => {
            this.career = singlCareerApi.data.data.careers[0];
        });
    },
};
</script>
